@import-normalize;
html {
  box-sizing: border-box;
  font-size: 62.5%;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body,
blockquote, figure,
form,
h1, h2, h3, h4, h5, h6,
ol, ul,
p, pre {
  margin: 0;
}

body {
  font-size: 1.6rem;
}

img {
  max-width: 100%;
}

ul {
  padding-left: 0;
}

button {
  border: 0;
  background: unset;
  cursor: pointer;
}

fieldset legend, header h1 {
  font-family: "Potta One", cursive;
  background: linear-gradient(180deg, var(--light-blue) 0%, var(--blue) 100%, #02343e 100%);
  background-clip: text;
  -webkit-background-clip: text;
  -moz-background-clip: text;
  -moz-text-fill-color: transparent;
  -webkit-text-fill-color: transparent;
}

:root {
  --light-blue: rgba(193,236,244,1);
  --blue: rgba(0,212,255,1);
  --black-alpha: rgba(0, 0, 0, .5);
}

body {
  color: var(--light-blue);
  font-family: "Poppins", sans-serif;
}

a {
  color: var(--blue);
  text-decoration: none;
}
@media (hover: hover) and (pointer: fine) {
  a:hover, a:focus {
    color: var(--light-blue);
  }
}
@media (hover: none) {
  a:active, a:focus {
    color: var(--light-blue);
  }
}

main {
  position: relative;
  top: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background: url("images/background.jpg") no-repeat fixed center center/cover;
}
main .overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: var(--black-alpha);
}

header {
  text-align: center;
}
header h1 {
  font-size: 6rem;
}

footer {
  margin-top: 1rem;
  padding: 1rem;
  text-align: center;
}

form {
  padding: 1rem;
}
@media (min-width: 900px) {
  form {
    width: 70%;
  }
}

fieldset {
  margin-top: -1.3rem;
  padding-bottom: 2rem;
  border-color: var(--blue);
  border-bottom: none;
}
@media (min-width: 480px) {
  fieldset {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
}
fieldset legend {
  font-size: 1.5em;
}
fieldset p {
  margin: 0.5rem;
  color: var(--blue);
}
fieldset label {
  padding: 0.5rem;
}

.campo {
  display: flex;
  flex-direction: column;
  padding: 0.5rem 0;
}
@media (min-width: 480px) {
  .campo {
    max-width: 45%;
  }
}
.campo label {
  color: var(--blue);
}

.radios,
.checkboxes {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  min-width: 100%;
}
.radios .radio,
.radios .checkbox,
.checkboxes .radio,
.checkboxes .checkbox {
  display: flex;
  flex-direction: column;
  padding: 0.5rem;
}
.radios .radio label,
.radios .checkbox label,
.checkboxes .radio label,
.checkboxes .checkbox label {
  max-width: 10rem;
  text-align: center;
}

@media (min-width: 480px) {
  fieldset p {
    flex: 1 0 100%;
  }

  #dropdown1 {
    min-width: 75%;
    justify-content: space-between;
  }

  #dropdown2 {
    min-width: 100%;
  }

  .names {
    width: 100%;
  }
}
textarea {
  height: 20rem;
  width: 100%;
  margin-top: 0.5rem;
}

#submit {
  width: calc(100% - 4px);
  height: 3em;
  margin-left: 2px;
  border: none;
  border-radius: 0 0 1rem 1rem;
  background: var(--blue);
  cursor: pointer;
  text-transform: uppercase;
}
@media (hover: hover) and (pointer: fine) {
  #submit:hover, #submit:focus {
    background: linear-gradient(180deg, var(--light-blue) 0%, var(--blue) 100%, #02343e 100%);
  }
}
@media (hover: none) {
  #submit:active, #submit:focus {
    background: linear-gradient(180deg, var(--light-blue) 0%, var(--blue) 100%, #02343e 100%);
  }
}